.exp {
    padding: 150px 0 50px;
    background-color: var(--color-white);
    @include small-tablet {
        padding: 50px 0 25px;
    }
    &__content {
        @include flex;
        max-height: 10rem;
        position: relative;
        overflow: hidden;
        &-title {
            color: var(--color-black);
            flex-basis: 40%;
            .exp__title-durationTotal {
                margin-top: 1rem;
                color: var(--color-greyDark);
                text-shadow: -5px 5px 5px rgba($color: #000, $alpha: .2);
            }
        }
        &-list {
            flex-basis: 60%;
        }
    }
}

.exp__list-item {
    @include pb (1rem);
    @include mb (1rem);
    color: var(--color-black);
    line-height: 2rem;
    &:not(:last-child) {
        border-bottom: 1px solid var(--color-grey);
    }
    @include mobile {
        line-height: 1rem;
    }
}

.exp__item-title {
    font-weight: 600;
    font-size: calc(16px + (28 - 16) * (100vw / 1310));
    @media (max-width: 767px) {
        font-size: calc(16px + ((28 - 16) + (28 - 16) * 0.7) * ((100vw - 320px) / 1310));
    }
    @include minMax (16, 28);
}

.exp__item-subtitle {
    font-size: calc(14px + (20 - 14) * (100vw / 1310));
    @media (max-width: 767px) {
        font-size: calc(14px + ((20 - 14) + (20 - 14) * 0.7) * ((100vw - 320px) / 1310));
    }
    @include minMax (14, 20);
}

.exp__item-time {
    @include flex;
    align-items: center;
    font-size: calc(12px + (18 - 12) * (100vw / 1310));
    @media (max-width: 767px) {
        font-size: calc(12px + ((18 - 12) + (18 - 12) * 0.7) * ((100vw - 320px) / 1310));
    }
    @include minMax (12, 18);
}

.exp__time-interval {
    color: var(--color-greyDark);
    margin-right: 5px;
}


