.running {
    padding: 4rem 0;
    background-color: var(--color-grey);
    display: flex;
    gap: 6rem;
    @include small-tablet {
        padding: 2rem 0;
    }
}

.running__content {
    //white-space: nowrap;
    display: flex;
    gap: 6rem;
    animation: run 15s linear infinite;
    //.running__content-text {
    //    margin-right: 6rem;
    //}
}

@keyframes run {
    from {
        transform: translateX(0);
    }
    to {
        transform: translateX(calc(-100% - 6rem));
    }
}
