a {
  position: relative;
  text-decoration: none;
  color: var(--color-black);
  transform: color var(--time);
}
a:hover:after {
  transform: scaleX(1);
}
a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -3px;
  left: 0;
  background-color: var(--color-blueNavy);
  transform: scaleX(0);
  transition: transform var(--time);
}
a::before {
  content: attr(data-hover);
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  max-height: 0;
  color: var(--color-blueNavy);
  transition: max-height var(--time);
}
a:hover::before {
  max-height: 100%;
}

