  background-color: var(--color-grey);
  padding: 4vmin 0;
  text-align: center;
}
.user__footer a {
  display: block;
  width: fit-content;
  margin: 2vmin auto;
  color: var(--color-white);
}
@media (max-width: 576px) {
  .user__footer a {
    margin: 4vmin auto;
    width: auto;
  }
}

.toback {
  position: fixed;
  bottom: 3vmin;
  left: 3vmin;
  padding: 1vmin;
  background-color: var(--color-black);
  border-radius: 5px;
}
@media (max-width: 576px) {
  .toback {
    bottom: 6vmin;
    left: 6vmin;
    padding: 3vmin;
  }
}
.toback svg {
  width: 2vmin;
  aspect-ratio: 1/1;
  fill: var(--color-white);
}