.user__header {
    background-color: var(--color-grey);
    padding: 4vmin 0;
    text-align: center;
    .section-title {
        color: var(--color-white);
    }
}

.user__content {
    background-color: var(--color-white);
    text-align: justify;
}

.user__container-subtitle {
    margin: 2vmin 0;
    text-align: center;
}

.user__container-text {
    //@include mb (2vmin);
    margin-bottom: 2vmin;
    line-height: 3vmin;
    @include mobile {
        line-height: 5vmin;
    }
    &::first-letter {
        margin-left: 2vmin;
    }
    a {
        color: var(--color-blueNavy);
    }
    //b {
    //    display: block;
    //    text-align: center;
    //}
}

.user__footer {
    background-color: var(--color-grey);
    padding: 4vmin 0;
    text-align: center;
    a {
        display: block;
        width: fit-content;
        margin: 2vmin auto;
        color: var(--color-white);
        @include mobile {
            margin: 4vmin auto;
            width: auto;
        }
    }
}
