.toback {
    position: fixed;
    bottom: 3vmin;
    left: 3vmin;
    padding: 1vmin;
    background-color: var(--color-black);
    border-radius: 5px;
    @include mobile {
        bottom: 6vmin;
        left: 6vmin;
        padding: 3vmin;
    }
    svg {
        width: 2vmin;
        aspect-ratio: 1/1;
        fill: var(--color-white);
    }
}
