.hero__title {
    @include flex;
    flex-direction: column;
    justify-content: center;
    padding: 100px 0;
    @include small-tablet {
        padding: 50px 0;
    }
    @include mobile {
        padding: 25px 0;
    }
}

.hero__title-myself {
    text-align: center;
    margin-bottom: 2rem;
}

.hero__myself-subtitle {
    @include flex;
    align-items: center;
    justify-content: space-around;
    @include small-tablet {
        flex-direction: column;
    }
    .hero__subtitle-content {
        color: var(--color-black);
        font-weight: 400;
        // flex-basis: 60%;
        text-align: justify;
    }
}

.hero__icon-image {
  //display: none;
  width: .5px;
  border-radius: 50%;
//  aspect-ratio: 1/1;
}

#myCanvas {
    display: none;
    margin-left: 8%;
    @include small-tablet {
      margin-left: 0;
    }
    @include mobile {
      width: 100px;
    }
}

.services {
  h2 {
    @include small-tablet {
        font-size: 25px;
    }
    @include mobile {
        font-size: 16px;
    }
  }
  .first {
    padding-bottom: 1.5rem;
    white-space: nowrap;
    text-align: end;
    @include mobile {
        padding-bottom: 1rem;
    }
  }
  .second {
    //transform: translateX(-20%);
    white-space: nowrap;

    //transform: translateX(20%);
    //transform: translateX(-100%);
    //animation: runningLineSecond 15s linear infinite;
  }
}

@keyframes runningLineFirst {
  to {
    transform: translateX(-100%);
  }
}

@keyframes runningLineSecond {
  to {
    transform: translateX(100%);
  }
}


