/* stylelint-disable */

@mixin bg-cover {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

$browser-context: 16; // Default
@function rem($pixels, $context: $browser-context) {
    @return #{$pixels/$context}rem;
}

@mixin image-set($pathToImage) {
    background-image: url("#{$pathToImage}.jpg");
    /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
    background-image: image-set(/* stylelint-disable-next-line function-comma-newline-after */
    "#{$pathToImage}.avif"type("image/avif"),
    "#{$pathToImage}.webp"type("image/webp"),
    "#{$pathToImage}.jpg"type("image/jpg"));
}

@mixin footerToBottom {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto 1fr auto;
    min-height: 100vh;
}

@mixin mr($value) {
    &:not(:last-child) {
        margin-right: $value;
    }
}

@mixin ml($value) {
    &:not(:last-child) {
        margin-left: $value;
    }
}

@mixin mb($value) {
    &:not(:last-child) {
        margin-bottom: $value;
    }
}

@mixin pb($value) {
    &:not(:last-child) {
        padding-bottom: $value;
    }
}

@mixin mt($value) {
    &:not(:last-child) {
        margin-top: $value;
    }
}

@mixin pseudo() {
    content: "";
    display: block;
}

@mixin none {
    display: none
}

@mixin minMax ($minFont,
$maxFont) {
    @media (min-width: 1630px) {
        font-size: #{$maxFont + px};
    }
    @media (max-width: 320px) {
        font-size: #{$minFont + px + "!important"};
    }
}

@mixin switchOn(){
    #switch__checkbox:checked ~ .switch__label {
      @content
    }
}
