:root {
    // base
    --font-family: 'Display', sans-serif;
    --content-width: 1280px;
    --container-offset: 15px;
    --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
    // colors
    //--color-white: #fff;
    --color-white: #000629;
    //--color-black: #000;
    --color-black: #fff;
    --color-grey: #f5f5f5;
    --color-greyDark: #a6a6a6;
    --color-greyLine: #d9d9d9;
    //--color-blueNavy: #1C008A;
    --color-blueNavy: #4FA2FC;
    --color-blueBereley: #0D2C54;
    --color-blueGlaucous: #6B7FD7;
    //--color-blueTiber: #0A2C42;
    --color-redBright: #AD0000;
    //--color-yellow: #c3b912;
    // times
    --time: .5s;
}
