.form {
    padding: 100px 0;
    background-color: var(--color-white);
    @include small-tablet {
        padding: 50px 0;
    }
    @include mobile {
        padding: 25px 0;
    }
}

//.form__contact {
//    @include flex-all-center;
//    flex-direction: column;
//}

.form__contact-title,
.form__contact-subtitle {
    text-align: center;
    margin-bottom: 2rem;
}

.form__contact-fill {
    width: 50%;
    position: relative;
    margin: 0 auto 2vmin;
    @include mobile {
        margin: 0 auto 4vmin;
    }
    &--textarea {
        display: block;
        width: 70%;
        height: 150px;
        line-height: 2rem;
        padding: 15px;
        margin: 0 auto 2vmin;
        border: 2px solid var(--color-blueNavy);
        border-radius: 10px;
        resize: none;
        &.focus-visible {
            box-shadow: 0px 0px 10px 2px rgba(34, 60, 80, 0.2);
        }
        @include mobile {
            font-size: 12px;
            line-height: 1rem;
            padding: 7px;
            margin: 0 auto 4vmin;
        }
    }
}

.form__fill-label {
    position: absolute;
    top: 5px;
    left: 5px;
    color: var(--color-blueNavy);
    border-radius: 10px;
    pointer-events: none;
    transition: var(--time);
    @include mobile {
        font-size: 12px;
    }
    span {
        font-weight: bold;
        color: var(--color-redBright);
    }
}

.form__fill-input {
    width: 100%;
    padding: 5px;
    border-bottom: 1px solid var(--color-black);
    border-radius: 10px;
    &.focus-visible + .form__fill-label,
    &:hover + .form__fill-label {
        //font-size: 16px;
        top: -12px;
        left: 7px;
        padding: 0 5px;
        background-color: var(--color-white);
        @media (min-width: 767px) {
            font-size: calc(10px + (15 - 10) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(10px + ((15 - 10) + (15 - 10) * 0.7) * ((100vw - 320px) / 1310));
        }
    }
}

.just-validate-error-label {
    position: absolute;
    left: 5px;
    bottom: -20px;
    @media (min-width: 767px) {
        font-size: calc(10px + (15 - 10) * (100vw / 1310));
    }
    @media (max-width: 767px) {
        font-size: calc(10px + ((15 - 10) + (15 - 10) * 0.7) * ((100vw - 320px) / 1310));
    }
    @media (min-width: 1630px) {
        font-size: 16px;
    }
    @media (max-width: 320px) {
        font-size: 10px !important;
    }
}

.form__contact-accords {
    width: 70%;
    text-align: center;
    margin: 0 auto 2vmin;
    transition: color var(--time);
    @include mobile {
        line-height: 16px;
        margin: 0 auto 4vmin;
    }
    a {
        color: var(--color-blueNavy);
    }
}

.form__contact-btn {
    display: block;
    padding: 20px 30px;
    margin: 0 auto;
    background-color: var(--color-greyDark);
    border-radius: 10px;
    cursor: not-allowed;
    transition: var(--time);
}
