@mixin mediaMaxH($height) {
    @media screen and (max-height: $height) {
      @content;
    }
}

  $bgPageClr: #131417;
  $bgCellClr: rgba(0, 0, 0, .5);
  $bgContentClr: #1C1E22;
  $cellHvrClr: #F25555;

  $rows: 2;
  $cellsInRow: 2;

  $cellAnSpeed: 400 * 1ms;
  $hoverDarkSpeed: 500 * 1ms;

    .box__section {
    position: relative;
    height: 100vh;
    background-color: var(--color-white);
    transition: box-shadow var(--time);
    @include mobile {
        height: 75vh;
    }
  }

  .box {
    display: flex;
    justify-content: flex-end;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 80%;
    height: 80%;
    // background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/537051/smoke_texture2714-min.jpg') center center no-repeat;
    background-size: cover;
    //box-shadow: 0 0 50px 0 rgba(75, 27, 27, .7);
    z-index: 1;
    overflow: hidden;
    @include small-tablet {
        width: 95%;
        height: 90%;
    }
    &__row {
      &-cell {
        position: absolute;
        top: 35%;
        left: 35%;
        //width: (50% - 2%) / $cellsInRow;
        width: 30%;
        height: (60% - 2%) / $rows;
        border-radius: 10px;
        transform: translateZ(0);
        transition: all $cellAnSpeed 600ms, width 300ms 300ms, background-color 300ms 600ms;
        cursor: pointer;
        overflow: hidden;
        z-index: 2;
        &[data-cell="1"] {
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), transparent), url('/img/consultation.jpg') center/cover no-repeat;
        }
        &[data-cell="2"] {
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), transparent), url('/img/escort.jpg') center/cover no-repeat;
        }
        &[data-cell="3"] {
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), transparent), url('/img/presentation.jpg') center/cover no-repeat;
        }
        &[data-cell="4"] {
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0.4), transparent), url('/img/outsourcing.jpg') center/cover no-repeat;
        }
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.5);
          opacity: 0;
          transition: $hoverDarkSpeed;
        }
        &:before {
          content: '';
          position: absolute;
          top: -5%;
          left: -5%;
          width: 110%;
          height: 110%;
          background-color: rgba(0, 0, 0, 0);
          opacity: 1;
          transition: 300ms;
          z-index: -3;
        }
        //&:hover:before {
        ////  background-color: var(--color-greyLine);
        //  transform: scale(1.1);
        //}
        &-info {
          margin-left: 20px;
          width: 50%;
          transition: $cellAnSpeed $cellAnSpeed + 450;
          @include mobile {
            width: 90%;
            margin-left: 5px;
          }
          &-ep {
            margin-top: 25px;
            //font-size: 10px;
            color: var(--color-grey);
            span {
              padding-left: 10px;
            }
            @include mediaMaxH(915px) {
              margin-top: 10px;
            }
          }
          &-title {
            font-weight: bold;
            //letter-spacing: 1.5px;
            line-height: 1.5rem;
            margin-top: 15px;
            color: var(--color-grey);
            text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
            @include mediaMaxH(692px) {
              margin-top: 8px;
            //  font-size: 18px;
            }
            @include mediaMaxH(692px) {
            //  font-size: 16px;
              margin-top: 4px;
            }
            @include mobile {
                word-wrap: break-word;
            }
          }
          &-sign {
            margin: 15px 0 0 2px;
            height: 3px;
            width: 20px;
            background-color: #fff;
            border-radius: 1px;
            transition: 200ms;
            z-index: 10;
            @include mediaMaxH(800px) {
              margin-top: 10px;
            }
          }
        }
        &-info--time {
          position: absolute;
          bottom: 18px;
          font-size: 10px;
          @include mediaMaxH(650px) {
            right: 20px;
            text-align: right;
          }
        }
        &-info--full {
          width: 80%;
          margin: 50px 0 0 90px;
          transform: translateX(-50px);
          opacity: 0;
          transition: 300ms;
          &-heading {
            line-height: 4rem;
            color: var(--color-grey);
            text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.8);
            text-transform: uppercase;
            @include mobile {
                line-height: 2rem;
                word-wrap: break-word;
            }
          }
          &-par {
            padding-top: 50px;
            color: var(--color-grey);
            line-height: 2rem;
          }
          @include small-tablet {
            margin: 25px 0 0 25px;
          }
          @include mobile {
            width: 92%;
            margin: 25px 0 0 10px;
          }
        }
      }
      @for $i from 1 through $cellsInRow {
        &-cell:nth-child(#{$i + 1}) {
        //  left: 50% + ((50% / $cellsInRow) * $i);
          left: 68%;
        }
      }
    }
    @for $i from 1 through $rows {
      &__row:nth-child(#{$i + 1}) {
        .box__row-cell {
          top: 35% + (65% / $rows) * $i;
        }
      }
    }
    &__content {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
    //  background-color: $bgContentClr;
      background-color: var(--color-white);
      transform: translateZ(0);
      opacity: 0;
      z-index: -10;
      transition: all 400ms 600ms, z-index 0ms 0ms;
      &-wrapper {
        position: relative;
        margin-left: auto;
        width: 50%;
        height: 100%;
        overflow: hidden;
      }
      &-inner {
        position: absolute;
        left: 18px;
        transform: translateX(50px);
        width: 100%;
        height: 100%;
        overflow: auto;
        opacity: 0;
        transition: $cellAnSpeed, opacity 200ms;
      }
      &-text {
        padding: 20px 0 30px 8px;
        width: 90%;
        max-width: 100%;
        &-heading {
          position: relative;
          margin: 50px 0 20px 0;
          color: var(--color-black);
          text-transform: uppercase;

          &:after {
            content: '';
            position: absolute;
            top: -15px;
            left: 0;
            width: 20px;
            height: 2px;
            background-color: var(--color-black);
          }
        }
        &-list {
            color: var(--color-black);
        }
        &-par,
        &-item,
        &-link {
          line-height: 22px;
          padding-bottom: 10px;
          @include mobile {
            line-height: 14px;
            padding-bottom: 5px;
          }
        }
        &-par a {
            //color: var(--color-blueNavy);
            text-decoration: underline;
        }
      }
    }
    &__close {
      position: absolute;
      top: 50px;
      left: 100px;
      height: 20px;
      width: 20px;
      font-size: 0;
      cursor: pointer;
      z-index: 9999;
      @include small-tablet {
        top: 30px;
        left: 50px;
      }
      @include mobile {
        top: 20px;
        left: 35px;
      }
      &:after,
      &:before {
        content: '';
        position: absolute;
        top: 9px;
        left: -14px;
        transform: rotate(45deg);
        width: 35px;
        height: 4px;
        //background-color: #fff;
        background-color: var(--color-grey);
        opacity: 0;
        transition: 500ms;
      }
      &:before {
        transform: rotate(-45deg);
      }
    }
    &__smokebg {
        display: none;
      position: absolute;
      top: -10%;
      left: -10%;
      transform: translate3d(0, 0, 0) rotate(0.01deg);
      width: 120%;
      height: 120%;
    //  background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/537051/smoke-min.png') center center no-repeat;
      background-size: cover;
      z-index: -10;
      opacity: .5;
    //  animation: smokebg 60s infinite alternate;
      @keyframes smokebg {
        25% {
          transform: translate3d(100px, 0, 0) rotate(0.01deg)
        }
        50% {
          transform: translate3d(75px, -25px, 0) rotate(0.01deg)
        }
        75% {
          transform: translate3d(45px, 30px, 0) rotate(0.01deg)
        }
        100% {
          transform: translate3d(25px, -15px, 0) rotate(0.01deg)
        }
      }
    }
    &__title {
      position: absolute;
      top: 25%;
      left: 9%;
      @include small-tablet {
        left: 5%;
      }
      @include mobile {
        left: 0;
      }
      &-heading {
        position: relative;
        color: var(--color-black);
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
        letter-spacing: 3px;
        line-height: 65px;
        font-weight: bold;
        text-transform: uppercase;
        &:after {
          content: '';
          position: absolute;
          top: 9vmin;
          left: 0;
          width: 25%;
          height: 2px;
          background-color: var(--color-black);
          @include mobile {
            top: 80%;
          }
        }
        //span {
        //  letter-spacing: 6px;
        //  color: #fff;
        //}
      }
      &-par {
        width: 50%;
        line-height: 1.5rem;
        margin-top: 20%;
        color: var(--color-black);
        @include small-tablet {
            margin-top: 15%;
        }
        @include mobile {
            font-size: 3.5vmin !important;
        }
      }
    }
  }

  .active {
    left: 0 !important;
    top: 0 !important;
    height: 100%;
    width: 50%;
    //background-color: $cellHvrClr;
    background-color: var(--color-grey);
    z-index: 5;
    transition: all $cellAnSpeed 400ms, width 300ms $cellAnSpeed + 400;
    cursor: default;
    opacity: 1 !important;
    @for $i from 1 through 4 {
      .box__row-cell-info:nth-child(#{$i}) {
        transform: translateX(50px);
        opacity: 0;
        transition: 300ms 50ms * ($i - 1) ease-in, opacity 375ms 50ms * ($i - 1);
      }
    }
    .box__row-cell-info--full {
      transform: translateX(0);
      opacity: 1;
      transition: 300ms 900ms;
    }
  }

  .cell-fade {
    opacity: 0;
    transition: all $cellAnSpeed 400ms, width 300ms $cellAnSpeed + 400, opacity 200ms 300ms;
  }

  .hover-cell {
    &:after {
      opacity: 1;
    }
  }

  .show-content {
    opacity: 1;
    z-index: 2;
    transition: all 300ms $cellAnSpeed, z-index 0ms 0ms;
    .box__content-inner {
      transform: translateX(0);
      opacity: 1;
      transition: 300ms 1100ms;
    }
  }

  .box-close-active {
    &:after,
    &:before {
      opacity: 1;
      transform: rotate(135deg);
      transition: 500ms 800ms;
    }
    &:before {
      transform: rotate(45deg);
    }
    span {
      opacity: 0;
      transition: 0ms 800ms;
    }
  }
