.aboutHero {
    background-color: var(--color-white);
    .container {
        padding: 100px var(--container-offset) 100px;
        @include small-tablet {
            padding: 50px 0 50px;
        }
    }
}

.aboutHero__title,
.aboutHero__subtitle {
    // width: 70%;
    text-align: justify;
    padding-bottom: 55px;
    margin: 0 21vmin;
    @include tablet {
        margin: 0 12vmin;
    }
    @include small-tablet {
        width: 100%;
        text-align: center;
    }
    span {
        font-weight: 900;
        text-shadow: 4px 3px 0px #4FA2FC, 9px 8px 0px rgba(0,0,0,0.15);
    }
}

.aboutHero__photos {
    //@include flex;
    display: none;
    gap: 2vmin;
    padding-bottom: 100px;
    //min-height: 677px;
    .photoA {
        flex-basis: 40%;
        .aboutHero__photoA-image {
            border-radius: 2vmin;
        }
    }
    .photoB {
        position: relative;
        flex-basis: 60%;
        transform: rotate(180deg);
        .aboutHero__photoB-image,
        .aboutHero__photoBB-image {
            max-width: 60%;
            aspect-ratio: 1/0.7;
            border-radius: 2vmin;
        }
        .aboutHero__photoB-image {
            position: absolute;
            bottom: 0;
            right: 0;
            //z-index: 1;
        }
        .aboutHero__photoBB-image {
            position: absolute;
            //transform: rotate(180deg);
        }
    }

}

.aboutHero__text {
    @include flex;
    justify-content: space-between;
    max-height: 14rem;
    overflow: hidden;
    @include tablet {
        max-height: 16rem;
    }

    @include mobile {
        gap: 10px;
    }
}

.aboutHero__text-title {
    color: var(--color-black);
}

.aboutHero__text-subtitle {
    position: relative;
    flex-basis: 65%;
    line-height: 2rem;
    @include small-tablet {
        line-height: 1.7rem;
    }
    @include mobile {
        line-height: 1.4rem;
    }
    p {
        @include mb(32px);
        @include small-tablet {
            margin-bottom: 1.5vmin !important;
        }
    }
}
