.header {
    background-color: var(--color-white);
}

.header__container {
    @include flex-all-sb;
    padding: var(--container-offset) 0;
    z-index: 2;
    transition: opacity var(--time);
    @include small-tablet {
        flex-wrap: wrap
    }
    @include mobile {
        gap: 10px;
    }
}

.header__container-logo {
    @include flex-all-center;
    .header__logo-svg {
        & svg {
            fill: var(--color-black);
        }
        @media (min-width: 767px) {
            margin-right: calc(5px + (10 - 5) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            margin-right: calc(5px + ((10 - 5) + (10 - 5) * 0.7) * ((100vw - 320px) / 1310));
        }
        @include mobile {
            transform: translateY(-5px);
        }
    }
}

.header__container-img {
    font-family: 'Roboto', sans-serif;
    @include flex-all-center;
    flex-direction: column;
    color: var(--color-black);
    .header__logo-subtitle {
        @media (min-width: 767px) {
            font-size: calc(8px + (16 - 8) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(8px + ((16 - 8) + (16 - 8) * 0.7) * ((100vw - 320px) / 1310));
        }
    }
    @include mobile {
        gap: 3px;
    }
}

.header__container-nav {
    @include small-tablet {
        order: 1;
    }
}

.header__container-email {
    font-weight: 600;
    @include small-tablet {
        flex-basis: 100%;
        transform: translateX(30%);
        order: 2;
        &::after {
            display: none;
        }
    }
}

.header__container-nav {
    .header__nav-link {
        &::before {
            top: 5px;
            @include small-tablet {
                display: none;
            }
        }
        &:first-child {
            margin-right: calc(5px + ((10 - 5) + (10 - 5) * 0.7) * ((100vw - 320px) / 1310));
        }
    }
}

#switch__checkbox {
    display: none;
}

.switch {
    //display: inline-block;
    display: none;
    position: relative;
    width: 65px;
    height: 35px;
    vertical-align: middle;
    overflow: hidden;
    margin-left: calc(5px + 8.5 * (100vw - 320px) / 1310);
    border: 5px solid #c3b912;
    border-radius: 45px;
    background-color: #fff;
    //margin-left: 2vmin / -2;
    //margin-top: 1vmin / -2;
    &__label {
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
        transition: var(--time);
    }
    &__circle {
        position: absolute;
        top: 3px;
        left: 4px;
        width: 20px;
        height: 20px;
        background-color: #d9d726;
        border-radius: 50%;
        //margin-top: 2px;
        //margin-left: 2px;
        transition: var(--time);
        //#switch__checkbox:not(checked) ~ .switch__label {
        //    animation: size-back 1s 1.6 linear;
        //}
        &:before {
            content: '';
            position: absolute;
            z-index: -1;
            top: -.5px;
            left: -16px;
            width: 18px;
            height: 18px;
            background-color: var(--color-white);
            border-radius: 50%;
            transition: var(--time);
            @include small-tablet {
                width: 14px;
                height: 17px;
            }
        }
        @include small-tablet {
            top: 2px;
            width: 16px;
            height: 16px;
        }

    }
    @include switchOn {
        transform: translateX(28px);
        //background-color: var(--color-white);
        //background-color: #1C243F;
        //animation: size 1s 1.6 linear;

        .switch__circle {
            //background-color: #1C008A;
            background-color: #fff;
            &:before {
                background-color: #000;
                z-index: 1;
                left: -6px;
            }
        }
        @include small-tablet {
            transform: translateX(17px);
        }
    }
    @include small-tablet {
        width: 50px;
        height: 27px;
        border: 3px solid #c3b912;
    }
}

.switch-bg {
    background-color: #000;
}

.switch-border {
    border: 5px solid #1C008A !important;
    @include small-tablet {
        border: 3px solid #1C008A !important;
    }
}


