.practice {
    padding: 50px 0 50px;
    background-color: var(--color-white);
    @include small-tablet {
        padding: 25px 0 25px;
    }
}

.practice__content {
    position: relative;
}

.practice__content-title {
    color: var(--color-black);
    margin: 50px 0;
    @include small-tablet {
        margin: 25px 0;
    }
}

.practice__slide {
    display: flex;
    position: relative;
    height: auto;
    border-radius: 20px;
    background-color: var(--color-black);
    &::before {
        content: '';
        position: absolute;
        bottom: 60px;
        width: 100%;
        height: 1px;
        background-color: var(--color-white);
    }
}

.practice__slide-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1;
    position: relative;
    padding: 2vmin;
    &::before {
        content: '';
        position: absolute;
        width: 40px;
        height: 40px;
        background: url('../img/svg/logo-favicon.svg') no-repeat;
        background-size: cover;
    }
}

.crash {
    &::after {
        content: 'банкротство';
        position: absolute;
        top: 25px;
        right: 0;
        font-size: 14px;
        padding: 2px 10px;
        color: var(--color-black);
        background-color: var(--color-redBright);
        clip-path: polygon(0.00% 0.00%,100.00% 0.00%,100.00% 100%,0.00% 100%,5% 50%);
        @include small-tablet {
            font-size: 12px;
        }
    }
}

.loss {
    &::after {
        content: 'ущерб';
        position: absolute;
        top: 25px;
        right: 0;
        font-size: 14px;
        padding: 2px 10px;
        color: var(--color-black);
        background-color: var(--color-blueBereley);
        clip-path: polygon(0.00% 0.00%,100.00% 0.00%,100.00% 100%,0.00% 100%,5% 50%);
        @include small-tablet {
            font-size: 12px;
        }
    }
}

.protest {
    &::after {
        content: 'оспаривание';
        position: absolute;
        top: 25px;
        right: 0;
        font-size: 14px;
        padding: 2px 10px;
        color: var(--color-black);
        background-color: var(--color-blueGlaucous);
        clip-path: polygon(0.00% 0.00%,100.00% 0.00%,100.00% 100%,0.00% 100%,5% 50%);
        @include small-tablet {
            font-size: 12px;
        }
    }
}

.practice__slide-desc {
    font-size: 16px;
    padding: 50px 0 35px;
    color: var(--color-white);
    line-height: 1.5rem;
    text-align: justify;
    @include small-tablet {
        font-size: 14px;
        line-height: 1.3rem;
    }
}

.practice__slide-link {
    font-size: 16px;
    width: fit-content;
    color: var(--color-white);
    @include small-tablet {
        font-size: 14px;
    }
}

.practice__btn {
    position: absolute;
    top: 35%;
    width: 30px;
    height: 30px;
    transform: rotate(45deg);
    background-color: var(--color-black);
    border-radius: 50px;
    border: 3px solid var(--color-white);
    &::after {
        content: '';
        width: 13px;
        height: 13px;
        border-left: 3px solid var(--color-blueNavy);
        border-bottom: 3px solid var(--color-blueNavy);
        transform: translate(3px, -3px);
    }
    &:nth-child(3) {
        left: -1px;
    }
    &:nth-child(2n) {
        right: -1px;
        transform: rotate(-135deg);
    }
    @include small-tablet {
        display: none;
    }
}
