.footer {
    position: relative;
    padding: 100px 0 50px;
    background-color: var(--color-grey);
    @include small-tablet {
        padding: 50px 0 25px;
    }
    @include mobile {
        padding: 25px 0 25px;
    }
}

.footer__top {
    @include flex-all-sb;
    padding-bottom: 50px;
    @include mobile {
        padding-bottom: 25px;
    }
}

.footer__top-offer {
    @include flex;
    flex-direction: column;
    margin-right: 3rem;
    @include small-tablet {
        width: 100%;
        margin-right: 0;
    }
}

.footer__offer-title {
    color: var(--color-blueNavy);
    @include small-tablet {
        text-align: center;
    }
}

.footer__offer-indicator {
    position: relative;
    width: 25px;
    aspect-ratio: 1/3;
    align-self: center;
    //opacity: 0;
    transition: opacity var(--time);
    use {
        position: absolute;
        animation: indicatorAnim 2s infinite linear;
    }
    .first {
        //transform: translateY(15%);
        animation-delay: 0s;
    }
    .second {
        animation-delay: 1s;
    }
}

@keyframes indicatorAnim {
    5% {
        opacity: 1;
        transform: translateY(-10%);
    }
    100% {
        opacity: 0;
        transform: translateY(40%);
    }
}


.footer__offer-email {
    &::before, &::after {
        display: none;
    }
    .text-stroke {
        //font-size: 92px;
        min-width: 65vw;
        height: 35px;
        letter-spacing: 4px;
        text-transform: uppercase;
        transform: translateX(35%);
        fill: none;
        stroke: #000;
        stroke-width: 2px;
        stroke-dasharray: 100%;
        stroke-dashoffset: 100%;
        //animation: textStrokeAnim 7s linear both;
        animation-iteration-count: 1;
    }
}

@keyframes textStrokeAnim {
    0% {
        stroke-dasharray: 100%;
        stroke-dashoffset: 100%;
        fill: #0000;
    }
    95% {
        stroke-dasharray: 0%;
        stroke-dashoffset: 90%;
        fill: #0000;
    }
    100% {
        stroke-dasharray: 0%;
        stroke-dashoffset: 0%;
        fill: #9b9dad;
        filter: drop-shadow(2px 2px 10px #0007);
    }
}

.footer__top-invite {
    position: absolute;
    top: 35%;
    right: 5%;
    width: 25%;
    line-height: 2rem;
    @include small-tablet {
        display: none;
    }
}

.footer__bottom-text {
    text-align: center;
}

.logo-svg {
    margin: 0 auto;
}
