    html {
        box-sizing: border-box;
    }

    *,
    *::before,
    *::after {
        box-sizing: inherit;
    }

    .page {
        height: 100%;
        font-family: var(--font-family, sans-serif);
        -webkit-text-size-adjust: 100%;
    }

    .page__body {
        margin: 0;
        min-width: 320px;
        min-height: 100%;
        font-size: 16px;
        background-color: var(--color-white);
    }

    img {
        height: auto;
        max-width: 100%;
        object-fit: cover;
    }

    a {
        position: relative;
        text-decoration: none;
        color: var(--color-black);
        transform: color var(--time);
        &:hover {
            //color: var(--color-blueNavy);
            &:after {
                transform: scaleX(1);
            }
        }
        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            bottom: -3px;
            left: 0;
            background-color: var(--color-blueNavy);
            transform: scaleX(0);
            transition: transform var(--time);
        }
        &::before {
            content: attr(data-hover);
            position: absolute;
            top: 0;
            left: 0;
            overflow: hidden;
            max-height: 0;
            color: var(--color-blueNavy);
            transition: max-height var(--time);
        }
        &:hover {
            &::before {
                max-height: 100%;
            }
        }
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    p {
        margin: 0;
        color: var(--color-black);
    }

    svg {
        width: inherit;
        height: inherit;
    }

    .site-container {
        overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
    }

    .is-hidden {
        display: none !important;
        /* stylelint-disable-line declaration-no-important */
    }

    .btn-reset {
        border: none;
        padding: 0;
        background-color: transparent;
        cursor: pointer;
    }

    .list-reset {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .input-reset {
        -webkit-appearance: none;
        appearance: none;
        border: none;
        border-radius: 0;
        background-color: #fff;
        &::-webkit-search-decoration,
        &::-webkit-search-cancel-button,
        &::-webkit-search-results-button,
        &::-webkit-search-results-decoration {
            display: none;
        }
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        /* display: none; <- Crashes Chrome on hover */
        -webkit-appearance: none;
        margin: 0;
        /* <-- Apparently some margin are still there even though it's hidden */
    }

    .visually-hidden {
        position: absolute;
        overflow: hidden;
        margin: -1px;
        border: 0;
        padding: 0;
        width: 1px;
        height: 1px;
        clip: rect(0 0 0 0);
    }

    .container {
        margin: 0 auto;
        padding: 0 var(--container-offset);
        max-width: var(--container-width);
    }

    .js-focus-visible :focus:not(.focus-visible) {
        outline: none;
    }

    .centered {
        text-align: center;
    }

    .dis-scroll {
        position: fixed;
        left: 0;
        top: 0;
        overflow: hidden;
        width: 100%;
        height: 100vh;
        overscroll-behavior: none;
    }

    .page--ios .dis-scroll {
        position: relative;
    }

    //myStyle
    .focus-visible {
        outline: none;
    }

    .focus-visible::before {
        position: absolute;
        content: '';
        left: -8px;
        top: -3px;
        right: -8px;
        bottom: -3px;
        z-index: -1;
    }

    .logo-svg {
        @media (min-width: 767px) {
            width: calc(25px + (60 - 25) * (100vw / 1310));
            height: calc(25px + (60 - 25) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            width: calc(25px + ((60 - 25) + (60 - 25) * 0.7) * ((100vw - 320px) / 1310));
            height: calc(25px + ((60 - 25) + (60 - 25) * 0.7) * ((100vw - 320px) / 1310));
        }
    }

    .section-title {
        @media (min-width: 767px) {
            font-size: calc(24px + (48 - 24) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(24px + ((48 - 24) + (48 - 24) * 0.7) * ((100vw - 320px) / 1310));
        }
        font-weight: 900;
        //line-height: 32px;
        @include minMax (24, 54);
        @include mobile {
            //line-height: 24px;
        }
    }

    .section-subtitle {
        @media (min-width: 767px) {
            font-size: calc(16px + (32 - 16) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(16px + ((32 - 16) + (32 - 16) * 0.7) * ((100vw - 320px) / 1310));
        }
        font-weight: 900;
        //line-height: 24px;
        @include minMax (16, 34);
        @include mobile {
            //line-height: 18px;
        }
    }

    .text {
        @media (min-width: 767px) {
            font-size: calc(15px + (24 - 15) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(15px + ((24 - 15) + (24 - 15) * 0.7) * ((100vw - 320px) / 1310));
        }
        font-weight: 400;
        line-height: 18px;
        @include minMax (15, 26);
        @include mobile {
            line-height: 8px;
        }
    }

    .minText {
        @media (min-width: 767px) {
            font-size: calc(10px + (15 - 10) * (100vw / 1310));
        }
        @media (max-width: 767px) {
            font-size: calc(10px + ((15 - 10) + (15 - 10) * 0.7) * ((100vw - 320px) / 1310));
        }
        font-weight: 400;
        line-height: 18px;
        @include minMax (10, 16);
        @include mobile {
            line-height: 8px;
        }
    }

    .d-fixed {
        position: fixed;
        width: 97%;
        max-width: var(--container-width);
        backdrop-filter: blur(5px);
    }

    .input-fixed {
        font-size: 12px;
        top: -12px !important;
        left: 7px !important;
        padding: 0 5px !important;
        //background-color: var(--color-white);
    }

    .btn-more {
        position: absolute;
        bottom: 0;
        right: 1rem;
        font-size: 1.5vmin;
        color: var(--color-white);
        padding: 8px 16px;
        border-radius: 50px;
        background-color: var(--color-blueNavy);
        background-image:
            radial-gradient(75% 50% at 50% 0%, #f4feff, transparent),
            radial-gradient(75% 35% at 50% 85%, #8de3fc, transparent);
        box-shadow:
            inset 0 -2px 4px 1px rgba(17, 110, 231, .6),
            inset 0 -4px 4px 1px rgba(141, 227, 252, 1),
            inset 0 0 2px 1px rgba(255, 255, 255, .2),
            0 1px 4px 1px rgba(17, 110, 231, .2),
            0 1px 4px 1px rgba(0, 0, 0, .1);
        transition: .3s;
            &::after {
                content: '';
                position: absolute;
                top: 1px;
                left: 50%;
                transform: translateX(-50%);
                border-radius: 50px;
                width: 80%;
                height: 40%;
                background-image: linear-gradient(to bottom, #f4feff, transparent);
                opacity: .75;
            }
            &:hover {
                transform: scale(1.04);
            }

            &:active {
                border-color: #0048d5;
                transform: scale(.96);
                background-color: #0048d5;
            }
    }

    @keyframes show {
        0% {
            opacity: 0;
        }
        50% {
            opacity: .5;
        }
        100% {
            opacity: 1;
        }
    }

    @keyframes fade {
        0% {
            opacity: 1;
        }
        50% {
            opacity: .5;
        }
        100% {
            opacity: 0;
        }
    }

    @keyframes upDown {
        from {
            transform: translateY(-101%);
        }
        to {
            transform: translateY(0);
        }
    }

    @keyframes downUp {
        from {
            transform: translateY(0);
        }
        to {
            transform: translateY(-101%);
        }
    }


